import React from 'react';
class Cart extends React.Component{
    render(){
        var CartCount = this.props.CartCount;
        console.log(CartCount);
        return(
            <div className="flex-container">
                <div className="OuterCart">
                {this.props.CartProp.map((dynamicComponent, i) => <CartContent
                                key = {i} componentData = {dynamicComponent} RemoveProp={this.props.RemoveCartProp}/>)}
                </div>
                
                
                {
                     (this.props.CartCount != 0)?( 
                        <div className="PriceDetails">
                            <PriceDetais PriceProp={this.props.CartProp} cartC={this.props.CartCount}/>
                        </div>
                        ) : ( 
                            <div>Cart is Empty!</div>
                        ) 
                }
               

            </div>
        )
    }
}

class CartContent extends React.Component{
    ControlUnitsI(e){
        var cqval = this.refs.cQ.value;
        this.refs.cQ.value= eval(parseInt(cqval)+1);
    }
    ControlUnitsD(e){
        var cqval = this.refs.cQ.value;
        this.refs.cQ.value= eval(parseInt(cqval)-1);
    }
    render(){
        return(
            <div className="innerCart">
            <div className="item_img2"><img src={require('../img/laptop.jpg')} alt="Item"/></div>
            <div>{this.props.componentData.cName}</div>
             <div>
                <b>&#8377;{this.props.componentData.ActPrice}</b>&nbsp;
               <strike>&#8377; {this.props.componentData.cPrice}&nbsp;</strike>
               <span>{this.props.componentData.cPrice}% off</span>
             </div>
             <div>
                 <button className="ctrlBtn" onClick={this.ControlUnitsD.bind(this)} >-</button>&nbsp;
                 <input type="number" className="CartQuantity" defaultValue="1" ref="cQ"/>&nbsp;
                 <button className="ctrlBtn" onClick={this.ControlUnitsI.bind(this)}>+</button>
             </div>
             <div><a href="javascript:void(0)" onClick={()=>this.props.RemoveProp(this.props.componentData.cName)}><b>REMOVE</b></a></div>
             </div>
        )
    }
}

class PriceDetais extends React.Component{
    render(){
        return(
            <div>
                <h4>PRICE DETAILS</h4>
                <hr />
                Price(1 item):900
                <br />
                Discount: 579
                <hr />
                <b>Total Payable:319</b>
            </div>
        )
    }
}
export default Cart;