import React from 'react';
class Home extends React.Component{
    render(){
        return(
            <div className="flex-container">
                <div className="innerWrap">
                    <Filters filterProp={this.changeFilter}/> 
                    <div className="RootCart">
                    <MySort />
                        {this.props.DataProp.map((dynamicComponent, i) => <Content 
                                key = {i} componentData = {dynamicComponent}  
                                updateCart={this.props.UpdateStateCart}
                                btnRef = {this.props.CbtnRef}
                                />)}
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}

class Content extends React.Component {
    render() {
       return (
          <div className="Cart_content">
            <div className="item_img"><img src={require('../img/laptop.jpg')} alt="Item"/></div>
             <div>{this.props.componentData.name}</div>
             <div>
               <b>&#8377; {this.props.componentData.price.actual}</b>&nbsp;
               <strike>{this.props.componentData.price.display}</strike>&nbsp;
               <span>{this.props.componentData.discount}% off</span>
             </div>
             
             <div className="OuterBtn">
             <button ref={this.props.btnRef} className="btn" id={this.props.componentData.name}
             onClick={(e)=> this.props.updateCart(this.props.componentData.name, this.props.componentData.price.display, this.props.componentData.price.actual, e)}
             >Add to cart</button>
             </div>
          </div>
       );
    }
  }

  class MySort extends React.Component{
    render(){
      return(
        <div className="sorting">
          <h4>Sort By</h4>
          <ul>
            <li><a onClick={this.props.sortHL}>Price--High Low</a></li>
            <li>Price--Low High</li>
            <li>Discount</li>
          </ul>
        </div>
      )
    }
  }
  

  class Footer extends React.Component{
    render(){
      return(
        <div className="footer">
         &copy;Copyright
        </div>
      )
    }
  }

  class Filters extends React.Component{
    render(){
      return(
        <div className="filters">
          <h3>Filters</h3>
          <input type="range" min="1" max="100" value="50" className="slider" id="myRange"></input>
          <p>Value: <span id="demo"></span></p>
          <button className="btn_a">Apply</button>
        </div>
      )
    }
  }


export default Home;